/* Стили для шапки */
header {
  background-color: #363333;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo img {
  width: 200px;
  margin-top: 5px;
}

nav ul {
  list-style: none;
  display: flex;
}

nav li {
  margin-right: 20px;
}

nav a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease-in-out;
}

nav a:hover {
  color: #ff9900;
}

.login {
  display: flex;
  align-items: center;
}

.login .Entrance {
  color: #fff;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 16px;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.login .Entrance:hover {
  background-color: #ff9900;
}

.user-info {
  display: flex;
  align-items: center;
  color: #fff;
  margin-left: 10px; /* Добавлено для отступа между текстом и кнопкой VK */
}

.user-info p {
  margin-right: 10px;
}

.user-avatar {
  width: 30px;
  border-radius: 50%;
}

.vkButton {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  cursor: pointer;
  border-radius: 8px;
  border: none;
  padding: 8px;
  background-color: #2787F5;
  margin-left: 5px; /* Добавлено для отступа между текстом и кнопкой VK */
}

.vkIcon {
  width: 25px;
}
