.game-selection {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.game-card {
  width: 300px;
  padding: 20px;
  margin: 20px;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #555cc0;
  transition: transform 0.3s ease, background-color 0.2s ease 0.2s;
}

.game-card:hover {
  background-color: #ff9900;
  transform: translateY(-5px);
}

.game-card img {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
}

.game-card h3 {
  font-size: 26px;
  margin-bottom: 10px;
  color: rgb(31, 28, 28);
}

.game-card p {
  font-size: 16px;
  color: rgb(31, 28, 28);
}
