/* Основные стили */

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;

  min-height: 100vh;
  background-color: #1e1e1e;
}

.container-1 {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.container-2 {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
}

.container-3 {
  background-color: rgba(255, 255, 255, 0.5);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

h1 {
  font-size: 36px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 20px;

}

.sapper-game {
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 2px;
  margin-bottom: 20px;
}

.cell {
  margin-bottom: 2px;
  border-radius: 4px;
  width: 50px;
  height: 50px;
  background-color: #333;
  border: 1px solid #555;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cell.mine {
  background-color: #f44336;
}

.cell.clicked {
  background-color: #61ff76;
  cursor: not-allowed;
}

.mines-selector {
  margin-bottom: 20px;
  text-align: center;
}

.mines-buttons {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.mines-buttons button {
  background-color: #333;
  padding: 6px 12px;
  margin: 0 4px;
  color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.mines-buttons button.selected {
  background-color: #ff9900;
  color: #fff;
  border-color: #ff9900;
}

.game-info {
  display: flex;
  justify-content: center;
}

.start-button,
.claim-button {
  padding: 10px 20px;
  background-color: #ff9900;
  border: none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  text-transform: uppercase;
  transition: background-color 0.3s ease, margin-left 0.3s ease;
}

.claim-button {
  background-color: #4caf50;
  margin-left: 10px;
}

.result-popup {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  animation: fade-in 0.5s ease;
}

.result-popup h2 {
  font-size: 24px;
  text-align: center;
  color: #333;
}

.cell:hover {
  transform: scale(1.02);
}

.additional-container {
  margin-right: 50px;
}

.custom-mines-input input {
  border-radius: 8px;
  height: 25px;
  width: 165px;
  border: none;
  color: white;
  background-color: #333;
}
.custom-mines-input button {
  margin-left: 5px;
  width: 30px;
  height: 25px;
  border-radius: 4px;
  border: none;
  color: white;
  background-color: #333;
}

/* Медиа-запросы */

@media screen and (max-width: 480px) {
  
  .additional-container {
    margin: 0;
  }

  .cell {
    width: 30px;
    height: 30px;
  }

  .sapper-game {
    flex-direction: column;
  }
}

@media screen and (min-width: 768px) {
  .game-board {
    grid-template-columns: repeat(6, 1fr);

  }

}
@media (max-width: 600px) {
  .sapper-game {
    flex-direction: column;
  }
  .additional-container {
    margin: 0;
  }

}
.error-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 5px;
  color: #fff;
  font-size: 20px;
  animation: fade-out-animation 8s forwards;
}

