/* DoorGame.css */

/* Корневой контейнер */
.door-game-container {

  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.doors {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.door {
  width: 150px;
  margin: 0 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.door:hover {
  transform: scale(1.1);
}

.door.open {
  animation: doorOpenAnimation 0.5s ease;
}

.door.bomb {
  animation: doorShakeAnimation 0.5s ease;
}

@keyframes doorOpenAnimation {
  0% {
    transform: scale(1) rotateY(0deg);
  }
  50% {
    transform: scale(1.2) rotateY(90deg);
  }
  100% {
    transform: scale(1) rotateY(180deg);
  }
}

@keyframes doorShakeAnimation {
  0% {
    transform: translateX(0px) rotateZ(0deg);
  }
  20% {
    transform: translateX(-5px) rotateZ(-5deg);
  }
  40% {
    transform: translateX(5px) rotateZ(5deg);
  }
  60% {
    transform: translateX(-5px) rotateZ(-5deg);
  }
  80% {
    transform: translateX(5px) rotateZ(5deg);
  }
  100% {
    transform: translateX(0px) rotateZ(0deg);
  }
}

.actions {
  display: flex;
  justify-content: center;
}

.actions button {
  margin: 0 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
}

.actions button:disabled {
  cursor: not-allowed;
}

.actions button:first-of-type {
  background-color: #4CAF50;
  color: white;
}

.actions button:nth-of-type(2) {
  background-color: #f44336;
  color: white;
}

.actions button:nth-of-type(3) {
  background-color: #2196F3;
  color: white;
}

.actions button:nth-of-type(4) {
  background-color: #FFC107;
  color: white;
}


@media (max-width: 600px) {
  .door {
    width: 50px;
  }
}

@media (max-width: 800px) {
  .door {
    width: 50px;
  }
}
