/* Общие стили для всего сайта */
body {
  background-color: #1e1e1e;
  color: #fff;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
}

/* Стили для контейнера мини-игры */
.wheel-container {

  max-width: 400px;
  text-align: center;
}

/* Стили для колеса */
.wheel {
  position: relative;
  width: 300px;
  height: 300px;
  margin: 0 auto;
  background-color: #282828;
  border-radius: 50%;
  transition: transform 1s ease-in-out;
  overflow: hidden;
}

.spinning {
  animation: spin-animation 3s infinite linear;
}

@keyframes spin-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Стили для внутреннего круга */
.inner-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  background-color: #444;
  border-radius: 50%;
}

/* Стили для выбора цвета */
.color-selection {
  align-items: center;
  margin-top: 20px;
}

h2 {
  text-align: center;
  font-size: 18px;
  margin-bottom: 40px;
}

.color-containers {
 
  display: flex;
  justify-content: center;
}

.color-container {
  position: relative;
  text-align: center;
  width: 40px;
  height: 40px;
  margin: 0 5px;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.color-container:hover {
  transform: scale(1.1);
}

.selected {
  border: 3px solid #fff;
}

/* Стили для кнопки "Начать" */
.spin-button {
  margin-top: 20px;
  height: 100px;
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ff9900;
  border: none;
  border-radius: 16px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.spin-button:disabled {
  background-color: #888;
  cursor: not-allowed;
}

.spin-button:hover:enabled {
  background-color: #ffbf40;
}

/* Стили для результата */
h2 {
  font-size: 20px;
  margin-top: 20px;
}

.result-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 5px;
  color: #fff;
  font-size: 20px;
  animation: fade-out-animation 8s forwards;
}

@keyframes fade-out-animation {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.wheel-game {
  align-items: center;
  margin-top: 100px;
  display: flex;
  justify-content: center;
}

.game-controls {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 20px;

}

.result-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 5px;
  color: #fff;
  font-size: 20px;
  animation: fade-out-animation 2s forwards;
}
.color-container > .text-x {
  color: #1e1e1e;
  position: absolute; 
  top: -5px; 
  left: 10px;
  font-size: 16px;
}
.text-X50 {
  color: #1e1e1e;
  position: absolute;  
  top: -5px;
  left: 5px;
  font-size: 16px;
}



@media screen and (max-width: 600px) {
  /* Add your styles for devices with a screen width of 600px or less */
  .wheel-container {
    margin-left: 0;
    max-width: 100%;
  }
  
  .wheel-game {
    
    flex-direction: column;
    align-items: center;
    
  }
  
  .game-controls {
    margin-right: 0;
    padding-left: 0;
    text-align: center;
  }
}

